body {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100vh;
    width: 100vw;
    margin: 0; }

#Login {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100vh;
    width: 100vw;
    margin: 0;

    img {
        margin: 74px 0 0 8%; }

    #login-form {
        display: flex;
        flex-direction: column;
        margin: 32px 0 0 0;

        h1 {
            margin: 0 0 20px 0; }

        #login {
            display: flex;
            flex-direction: column;
            align-items: center;

            img {
                margin: 0 17px 0 0; }

            input {
                min-height: 50%;
                border-style: none;
                border-bottom-style: solid;
                border-radius: 0;
                width: 100%; }

            .box {
                display: flex;
                flex-direction: row;
                justify-content: center;
                align-items: center;
                height: 7vh;
                width: 70.66vw;
                margin: 0 0 2vh 0; }

            .box:nth-child(2) {
                margin: 0 0 2vh 0; }

            p {
                font-size: 11px;
                text-align: right;
                margin: 0 0 10% 0; }

            button {
                background: url(../image/login/btn_Login.png) no-repeat center / contain;
                width: 80%;
                height: 25%;
                border: none; } } } }





#SignUp {
    display: none; }
